"use client";

import { useQuery,useQueryClient  } from "@tanstack/react-query";
import { api } from "@/lib/api";
import { 
  Plus, Package, Edit3, Trash2, Search, Filter, Loader2, ExternalLink
} from "lucide-react";
import Link from "next/link";
import { toast } from "react-hot-toast/headless";
import { queryClient } from "@/lib/react-query";

export default function VendorInventoryPage() {
  const queryClient = useQueryClient();

  // 1. DATA FETCHING HOOK
  const { data: products, isLoading } = useQuery({
    queryKey: ["vendor-products"],
    queryFn: async () => {
      const res = await api.get("/products/vendor/my-products"); 
      return res.data;
    }
  }); // <--- Make sure this closing brace and parenthesis are here!

  // 2. DELETE HANDLER (Must be below the hooks, but above the 'return')
  const handleDelete = async (productId: string) => {
    if (!window.confirm("Are you sure you want to delete this product?")) return;
    try {
      await api.delete(`/products/${productId}`);
      toast.success("Product deleted");
      queryClient.invalidateQueries({ queryKey: ["vendor-products"] });
    } catch (err) {
      toast.error("Failed to delete product");
    }
  };

  return (
    <div className="max-w-7xl mx-auto py-10 px-6">
      <div className="flex flex-col md:flex-row md:items-center justify-between gap-4 mb-10">
        <div>
          <h1 className="text-3xl font-black text-slate-900 tracking-tight">Product Inventory</h1>
          <p className="text-slate-500 font-medium">Manage your catalog and bulk pricing tiers.</p>
        </div>
        
        <Link 
          href="/vendor/products/create" 
          className="flex items-center justify-center gap-2 px-6 py-4 bg-blue-600 text-white rounded-2xl font-bold hover:bg-blue-700 transition-all shadow-xl shadow-blue-100"
        >
          <Plus size={20} /> Add New Product
        </Link>
      </div>

      {/* Stats Overview */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
        <div className="bg-white p-6 rounded-[2rem] border border-slate-100 shadow-sm">
          <p className="text-slate-400 text-xs font-black uppercase tracking-widest mb-1">Total Listings</p>
          <p className="text-3xl font-black text-slate-900">{products?.length || 0}</p>
        </div>
      </div>

      {/* Inventory Table */}
      <div className="bg-white rounded-[2.5rem] border border-slate-100 shadow-xl shadow-slate-200/50 overflow-hidden">
        <div className="overflow-x-auto">
          <table className="w-full text-left border-collapse">
            <thead>
              <tr className="bg-slate-50/50 border-b border-slate-100">
                <th className="p-6 text-xs font-black uppercase text-slate-400">Product</th>
                <th className="p-6 text-xs font-black uppercase text-slate-400">Category</th>
                <th className="p-6 text-xs font-black uppercase text-slate-400">Base Price</th>
                <th className="p-6 text-xs font-black uppercase text-slate-400">MOQ</th>
                <th className="p-6 text-xs font-black uppercase text-slate-400 text-right">Actions</th>
              </tr>
            </thead>
           {/* Inside the tbody of your Inventory Page */}
<tbody className="divide-y divide-slate-50">
  {isLoading ? (
    <tr>
      <td colSpan={5} className="py-20 text-center">
        <Loader2 className="animate-spin mx-auto text-blue-600" size={32} />
      </td>
    </tr>
  ) : products && products.length > 0 ? (
    // ðŸš€ THIS IS THE PART THAT WAS MISSING:
    products.map((product: any) => (
      <tr key={product._id} className="hover:bg-slate-50/50 transition-colors group">
        <td className="p-6">
          <div className="flex items-center gap-4">
            <div className="w-12 h-12 bg-slate-100 rounded-xl overflow-hidden border border-slate-100">
              {product.images?.[0] ? (
                <img src={product.images[0]} className="w-full h-full object-cover" alt="" />
              ) : (
                <Package className="w-full h-full p-2 text-slate-300" />
              )}
            </div>
            <span className="font-bold text-slate-900">{product.name}</span>
          </div>
        </td>
        <td className="p-6 text-sm text-slate-600">{product.category}</td>
        <td className="p-6 font-bold text-slate-900">${product.pricePerUnit}</td>
        <td className="p-6 font-bold text-slate-500">{product.minimumOrderQuantity} units</td>
        <td className="p-6 text-right">
          <div className="flex justify-end gap-2">
            <Link href={`/vendor/edit/${product._id}`} className="p-3 bg-slate-50 text-slate-400 hover:text-blue-600 hover:bg-white hover:shadow-md rounded-xl transition-all">
              <Edit3 size={18} />
            </Link>
            <button onClick={() => handleDelete(product._id)} className="p-3 bg-slate-50 text-slate-400 hover:text-red-600 hover:bg-white hover:shadow-md rounded-xl transition-all">
              <Trash2 size={18} />
            </button>
          </div>
        </td>
      </tr>
    ))
  ) : (
    /* This only shows if products.length is 0 */
    <tr>
      <td colSpan={5} className="py-24 text-center">
        <div className="flex flex-col items-center">
          <Package className="text-slate-200 mb-4" size={48} />
          <p className="text-slate-900 font-black text-xl">No products found</p>
          <Link href="/vendor/products/create" className="text-blue-600 font-bold mt-2">
            Add your first product listing â†’
          </Link>
        </div>
      </td>
    </tr>
  )}
</tbody>
          </table>
        </div>
      </div>
    </div>
  );
}